export const DATABASE = firebase.database();

export function writeInDB(ref, data) {
  DATABASE.ref(ref).set(data)
    .then(v => console.log(v))
    .catch(err => console.error(err));
}

export function Update(ref, data) {
  DATABASE.ref(ref).update(data)
    .then(e => console.log("Update successful"))
    .catch(err => console.error(err));
}

export const readFromDB = async (ref) => {
  return DATABASE.ref(ref).get();
}
