import React, { useState } from "react";
import "../pages/Contact.css";
import Header from "../component/Header";
import { sendContact } from "../controll/sendEmail";
import { handleFormValidation } from "../controll/sendEmail";
import img from '../img/imoji.jpg'
export default function Contact() {
  const [form, setForm] = useState({ email: "", name: "", msg: "" });
  const [error, seterror] = useState(false);
  const [sended, setSended] = useState(false);
  return (
    <div className="contact">
      <Header activePage={"Contact"} />
      <div className="contact-div">
        <h1>contact</h1>
        <p>
          if you want any help or you find any problem , you can contact our team SMP
            , send  email haha here :) :
        </p>
        <form
          className="contact-form"
          onSubmit={function (e){
            e.preventDefault();
            if (handleFormValidation({ ...form })) {
              sendContact({ ...form, setSended });
              setForm({ email: "", name: "", msg: "" });
              setTimeout(()=>{
                setSended((t) => false);
              }, 5000)
            } else {
              seterror((t) => true);
              setTimeout(()=>{
                seterror((t) => false);
              }, 3000)
            }
          }}
        >
          {error && (
            <span className="error">
              email does'nt valid or one of fields are empty , check that :({" "}
            </span>
          )}
          {sended && (
            <span className="error" style={{color : 'green'}}>
              send sucess thanks you for contact ours team
            </span>
          )}
          <input
            type="text"
            value={form.name}
            id="name"
            placeholder="name"
            onChange={(e) => {
              setForm(() => ({ ...form, name: e.target.value }));
            }}
          />
          <input
            type="text"
            id="email"
            value={form.email}
            placeholder="email"
            onChange={(e) => {
              setForm(() => ({ ...form, email: e.target.value }));
            }}
          />
          <textarea
            name=""
            id="msg"
            cols="30"
            value={form.msg}
            rows="10"
            placeholder="message..."
            onChange={(e) => {
              setForm(() => ({ ...form, msg: e.target.value }));
            }}
          ></textarea>
          <input type="submit" value="send" style={{color : "black"}} />
        </form>
      </div>
    
    </div>
  );
}
