import React, { useEffect, useState } from 'react';
import Header from '../component/Header';
import '../pages/Controll.css';
import { readFromDB } from '../controll/database';
import Button from '../component/Button';

export default function Controll() {
  const [rooms, setRooms] = useState({ room1: null, room2: null, room3: null });
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    (async function read() {
      try {
        const res = await readFromDB('/relay');
        const relays = await res.val();
        console.log("relay" , relays)
        setRooms({
          room1: relays?.room1,
          room2: relays?.room2,
          room3: relays?.room3,
        });
        setLoading(false);
      } catch (error) {
        console.log(error.message);
      }
    })();
  }, []);

  const handleUpdate = (id, value) => {
    setRooms((prevRooms) => ({ ...prevRooms, [id]: value }));
  };

  return (
    <>
      {loading ? (
        <div>Loading...</div>
      ) : (
        <div className="controll-data">
          <Header activePage={"controll"} />
          <div className="controll">
            <h1>Control your rooms</h1>
            <div>
              <span>Room 1 status: </span>
              <Button id="room1" setUpdate={(value) => handleUpdate('room1', value)} room={rooms.room1} />
            </div>
            <div>
              <span>Room 2 status: </span>
              <Button id="room2" setUpdate={(value) => handleUpdate('room2', value)} room={rooms.room2} />
            </div>
            <div>
              <span>Room 3 status: </span>
              <Button id="room3" setUpdate={(value) => handleUpdate('room3', value)} room={rooms.room3} />
            </div>
          </div>
        </div>
      )}
    </>
  );
}
