import React, { useState, useEffect } from 'react';
import './Room.css';
import { readFromDB } from '../controll/database';

export default function Room({ CurrentRoom, parentRender }) {
  const [current, setCurrent] = useState(-2.5);
  const [power, setPower] = useState(0);
  const [voltage, setVoltage] = useState(0);

  useEffect(() => {
      async    function read (){
       const current =  await readFromDB(`currents/room${CurrentRoom}`);
           console.log( "current" , current.val())
        const Vcurrent = await current.val()
            setCurrent ( t =>  Vcurrent)
            const power = await  readFromDB(`power/room${CurrentRoom}`);
            const Vpower = await power.val()
            setPower(t => Vpower)
            const Volage = await  readFromDB(`/voltage`, setVoltage);
            const VVolate = await Volage.val()
            setVoltage(t => VVolate)
        }
        read()
  }, [parentRender]);

  return (
    <div className='single-Room'>
      <h1>Room {CurrentRoom}</h1>
      <div className='room-data'>
        <div>
          Current:
          <span>{current} A</span>
        </div>
        <div>
          Power:
          <span>{power} WH</span>
        </div>
        <div>
          Voltage:
          <span>{voltage} V</span>
        </div>
      </div>
    </div>
  );
}
