import React, { useState, useEffect } from "react";
import "./ViewsRooms.css";
import Room from "../component/Room";
import Header from "../component/Header";

export default function ViewsRooms() {
  const [currentRoom, setRoomIndex] = useState(1);
  const [updateRoom, setUpdate] = useState(false);

  function descRoomIndex() {
    if (currentRoom > 1) {
      setRoomIndex((t) => t - 1);
    }
  }

  function ascRoomIndex() {
    if (currentRoom < 3) {
      setRoomIndex((t) => t + 1);
    }
  }

  useEffect(() => {
    const interval = setInterval(() => {
      setUpdate((t) => !t);
    }, 5000);
    return () => clearInterval(interval);
  }, []);

  return (
    <div className="data-room">
      <Header activePage={"views"} />
      <div className="room">
        <button onClick={descRoomIndex}>Prev</button>
        <Room CurrentRoom={currentRoom} parentRender={updateRoom} />
        <button onClick={ascRoomIndex}>Next</button>
      </div>
    </div>
  );
}
