import React, { useEffect, useState } from "react";
import "../component/Button.css";
import { Update } from "../controll/database";

export default function Button({ id, setUpdate, room }) {
  const [r, setR] = useState(room);

  useEffect(() => {
    setR(room); // Ensure the state is synced with the initial prop
  }, [room]);

  useEffect(() => {
    if (r !== null) {
      Update(`/relay`, { [id]: r });
    }
  }, [r, id]);

  const handleChange = () => {
    setR((prevR) => !prevR);
    setUpdate(!r);
  };

  return (
    <div className="btn">
      <label htmlFor={id} className={!r ? "off" : "on"}></label>
      <input
        hidden
        value={r}
        onChange={handleChange}
        type="checkbox"
        id={id}
      />
    </div>
  );
}
