import './App.css';
import Home from './pages/Home';
import Contact from './pages/Contact';
import ViewsRooms from './pages/ViewsRooms'
import Controll from './pages/Controll';
import ContextData, { useData } from './ContextData';
import Reducer from './Reducer';
import{ DATA }    from './Reducer';
import { createBrowserRouter as Router  , RouterProvider } from 'react-router-dom';


   const ROUTE = Router([
    {
     path : '/' , 
     element :  <Home></Home>,
   //  loader :<Audio height={80} width={80}  radius={9}  ariaLabel='loading' />
     
    },
    {
      path : '/Contact' , 
      element :  <Contact></Contact>,
     // loader :<Audio height={80} width={80}  radius={9}  ariaLabel='loading' />
      
     },
     {
      path : '/viewsRooms' , 
      element :  <ViewsRooms></ViewsRooms>,
     // loader :<Audio height={80} width={80}  radius={9}  ariaLabel='loading' />
      
     },
     {
      path : '/ControllRoom' , 
      element :  <Controll></Controll>,
     // loader :<Audio height={80} width={80}  radius={9}  ariaLabel='loading' />
      
     }
   ])
function App() {
  return (
    <div className="App">
        <ContextData reducer={Reducer} data={DATA}>
              <RouterProvider router={ROUTE}>
              </RouterProvider>
        </ContextData>
    </div>
  );
}

export default App;
   