import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import "../component/Header.css";

export default function Header({ activePage }) {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  return (
    <div className='app-Header ' style={{padding : '10px'}}>
      <div className='menu-toggle' onClick={toggleMenu}>
        {isMenuOpen ? '' : ''}
      </div>
      <ul className={`list ${isMenuOpen ? 'open' : ''}`}>
        <li className={activePage === "home" ? "active" : ""}>
          <Link to='/'>Home</Link>
        </li>
        <li className={activePage === "views" ? "active" : ""}>
          <Link to='/viewsRooms'>Rooms Data</Link>
        </li>
        <li className={activePage === "controll" ? "active" : ""}>
          <Link to='/ControllRoom'>Control Rooms</Link>
        </li>
        <li className={activePage === "Contact" ? "active" : ""}>
          <Link to='/Contact'>Contact</Link>
        </li>
      </ul>
    </div>
  );
}
